/*
==============================================
pulse
==============================================
*/
.pulse {
  animation-name: pulse;
  -webkit-animation-name: pulse;
  animation-duration: 1.5s;
  -webkit-animation-duration: 1.5s;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
}
@keyframes pulse {
  0% {
    transform: scale(0.8);
    opacity: 0.7;
  }
  50% {
    transform: scale(1.1);
    opacity: 1;
  }
  100% {
    transform: scale(0.8);
    opacity: 0.7;
  }
}
@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0.8);
    opacity: 0.7;
  }
  50% {
    -webkit-transform: scale(1.1);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(0.8);
    opacity: 0.7;
  }
}
/*
==============================================
swing
==============================================
*/
.swing {
  animation-name: swing;
  -webkit-animation-name: swing;
  animation-duration: 1.5s;
  -webkit-animation-duration: 1.5s;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
}
@keyframes swing {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(50%);
  }
  100% {
    transform: translateY(0%);
  }
}
@-webkit-keyframes swing {
  0% {
    -webkit-transform: translateY(0%);
  }
  50% {
    -webkit-transform: translateY(50%);
  }
  100% {
    -webkit-transform: translateY(0%);
  }
}
/*
==============================================
sway
==============================================
*/
.sway {
  animation-name: sway;
  -webkit-animation-name: sway;
  animation-duration: 1.5s;
  -webkit-animation-duration: 1.5s;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
}
@keyframes sway {
  0% {
    transform: rotate(-8deg);
  }
  50% {
    transform: rotate(8deg);
  }
  100% {
    transform: rotate(-8deg);
  }
}
@-webkit-keyframes sway {
  0% {
    -webkit-transform: rotate(-8deg);
  }
  50% {
    -webkit-transform: rotate(8deg);
  }
  100% {
    -webkit-transform: rotate(-8deg);
  }
}
/*
==============================================
spin
==============================================
*/
.spin {
  -webkit-animation: spin 50s infinite linear;
  -moz-animation: spin 50s infinite linear;
  -ms-animation: spin 50s infinite linear;
  -o-animation: spin 50s infinite linear;
  animation: spin 50s infinite linear;
}
@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}
@-moz-keyframes spin {
  to {
    -moz-transform: rotate(360deg);
  }
}
@-ms-keyframes spin {
  to {
    -ms-transform: rotate(360deg);
  }
}
@-o-keyframes spin {
  to {
    -o-transform: rotate(360deg);
  }
}
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
/*
==============================================
Alt Spin
==============================================
*/
.spin-alt {
  -webkit-animation: spin-alt 50s infinite linear;
  -moz-animation: spin-alt 50s infinite linear;
  -ms-animation: spin-alt 50s infinite linear;
  -o-animation: spin-alt 50s infinite linear;
  animation: spin-alt 50s infinite linear;
}
@-webkit-keyframes spin-alt {
  to {
    -webkit-transform: rotate(-360deg);
  }
}
@-moz-keyframes spin-alt {
  to {
    -moz-transform: rotate(-360deg);
  }
}
@-ms-keyframes spin-alt {
  to {
    -ms-transform: rotate(-360deg);
  }
}
@-o-keyframes spin-alt {
  to {
    -o-transform: rotate(-360deg);
  }
}
@keyframes spin-alt {
  to {
    transform: rotate(-360deg);
  }
}
/*
==============================================
Letter Flash
==============================================
*/
.letterFlash {
  -webkit-animation-name: letter-flash;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-duration: 2400ms;
  -webkit-animation-duration: 3.2s;
  -webkit-animation-timing-function: ease;
  -webkit-animation-fill-mode: none;
}
@keyframes letter-flash {
  0% {
    opacity: 0.35;
  }
  30% {
    opacity: 0.8;
  }
  40% {
    opacity: 0.45;
  }
  100% {
    opacity: 0.21;
  }
}
@-webkit-keyframes letter-flash {
  0% {
    opacity: 0.35;
  }
  30% {
    opacity: 0.8;
  }
  40% {
    opacity: 0.45;
  }
  100% {
    opacity: 0.21;
  }
}
